<template>
    <div id="mall-basic">
        <public-header title="基础设置"></public-header>
        <div class="content">
            <van-radio-group v-model="mall.store_orderby_type">
                <van-cell-group>
                    <van-cell title="首页商家排序方式"></van-cell>
                    <van-cell title="按排序大小" clickable @click="mall.store_orderby_type = 'displayorder'" class="border-0px">
                        <van-radio name="displayorder" />
                    </van-cell>
                    <van-cell title="按热度" clickable @click="mall.store_orderby_type = 'click'" class="border-0px">
                        <van-radio name="click" />
                    </van-cell>
                    <van-cell title="离我最近" clickable @click="mall.store_orderby_type = 'distance'" class="border-0px">
                        <van-radio name="distance" />
                    </van-cell>
                    <van-cell title="销量最高" clickable @click="mall.store_orderby_type = 'sailed'" class="border-0px">
                        <van-radio name="sailed" />
                    </van-cell>
                    <van-cell title="评分最高" clickable @click="mall.store_orderby_type = 'score'" class="border-0px">
                        <van-radio name="score" />
                    </van-cell>
                </van-cell-group>
            </van-radio-group>
            <van-cell-group  class="margin-10-t">
                <van-cell>
                    <div slot="title">超出商家配送范围是否展示</div>
                    <iswitch :value="mall.store_overradius_display" condition-open="1" condition-close="2" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="mall.store_overradius_display" ></iswitch>
                </van-cell>
            </van-cell-group>
            <div class="padding-15">
                <van-button size="normal" class="bg-primary font-16" block @click="onSubmit">提交</van-button>
            </div>
        </div>
        <iloading v-if="showPreLoading"></iloading>
    </div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				showPreLoading: true,
                mall: {
					store_orderby_type: 'displayorder',
					store_overradius_display: '1'
                }
			}
		},
		components: {
			publicHeader,
			iswitch
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: this,
					autoAssign: true,
					variable: 'mall',
					url: 'plateform/agent/config/mall'
				});
			},

			jsToggleSwitch(res){
				this.util.jsToggleSwitch({
					vue: this,
					key: res.key || res.keys,
					value: res.value
				});
            },

            onSubmit(){
				var that = this;
				setData({
					vue: this,
					url: 'plateform/agent/config/mall',
					data: that.mall
				});
            }
		},
        mounted(){
			this.onLoad();
        }
	}
</script>

<style>

</style>


